import Vue from "vue";
import Router from "vue-router";
import Registery from "./views/Register.vue";
import LoginForm from "./views/LoginForm.vue";
import Home from "./components/Home.vue";
import Main from "./components/Main.vue";

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/reform",
      name: "reg",
      component: Registery
    },
    {
      path: "/loginform",
      name: "loginfor",
      component: LoginForm
    },
    {
      path: "/home",
      name: "hom",
      component: Home
    },
    {
      path: "/",
      name: "main",
      component: Main
    }
  ]
});
