<template>
  <div id="app1">
    <img class="logomain" :class="{ 'hidden-logo': !showNavbar }" src="../assets/logomain.png">
    <div class="navbar" :class="{ 'hidden-navbar': !showNavbar }">
      <ul>
        <li>
          <router-link to="/home">TRANG CH</router-link>
        </li>
        <li>
          <a href="#" v-scroll-to="'.content2'">GII THIU</a>
        </li>
        <li>
          <a href="#" v-scroll-to="'.content3'">NHN VT</a>
        </li>
        <li>
          <a href="#">CNG NG CHNH THC</a>
        </li>
        <div id="logoline">
          <img id="logoes" style="margin-right:10px" src="../assets/fblogo.jpg">
          <img id="logoes" style="margin-right:10px" src="../assets/twlogo.png">
          <img id="logoes" src="../assets/lilogo.png">
        </div>
      </ul>
    </div>
    <button class="totop" :class="{ 'hidden-totop': !showNavbar}" @click="backToTop">^</button>
  </div>
</template>
<script>
const OFFSET = 60;
export default {
  data() {
    return {
      showNavbar: true,
      lastScrollPosition: 0
    };
  },

  mounted() {
    window.smoothscroll = () => {
      let currentScroll =
        document.documentElement.scrollTop || document.body.scrollTop;
      if (currentScroll > 0) {
        window.requestAnimationFrame(window.smoothscroll);
        window.scrollTo(0, Math.floor(currentScroll - currentScroll));
      }
    };
    window.addEventListener("scroll", this.onScroll);
  },

  beforeDestroy() {
    window.removeEventListener("scroll", this.onScroll);
  },
  methods: {
    onScroll() {
      if (window.pageYOffset < 0) {
        return;
      }
      if (Math.abs(window.pageYOffset - this.lastScrollPosition) < OFFSET) {
        return;
      }
      this.showNavbar = window.pageYOffset < this.lastScrollPosition;
      this.lastScrollPosition = window.pageYOffset;
    },
    backToTop() {
      window.smoothscroll();
      this.$emit("scrolled");
    }
  }
};
</script>

<style scoped>
a {
  color: black;
  text-decoration: none;
}
a:hover {
  color: dodgerblue;
}
#logoline {
  margin-left: -10px;
}
#logoes {
  margin-top: 50px;

  width: 40px;
  height: 40px;
}
li {
  list-style-type: none;
  width: 150px;
  margin-left: -10%;
  font-weight: bold;
  font-size: 20px;
  padding: 10px;
  text-align: center;
}
.logomain {
  top: 60px;
  left: 45px;
  width: 150px;
  height: 110px;
  position: fixed;
  z-index: 10;
}
.logomain.hidden-logo {
  display: none;
}
.totop {
  border-radius: 5px;
  background-color: rgb(1, 14, 27);
  background-color: rgba(1, 14, 27, 0.7);
  position: fixed;
  width: 45px;
  height: 45px;
  display: block;
  right: 15px;
  bottom: 15px;
  border: none;
  color: white;
  font-size: 22px;
  text-align: center;
}
.totop.hidden-totop {
  display: none;
}
.navbar {
  height: 100%;
  width: 240px;
  background: white;
  opacity: 0.6;
  position: fixed;
  box-shadow: 0 2px 15px rgba(71, 120, 120, 0.5);
  transform: translate3d(0, 0, 0);
  transition: 0.1s all ease-out;
}

.navbar.hidden-navbar {
  display: none;
  transform: translate3d(0, -100%, 0);
}

.message {
  position: fixed;
  left: 50vw;
  top: calc(50vh + 60px);
  transform: translate(-50%, -50%);
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 5vw;
  text-align: center;
  color: hsl(200, 50%, 60%);
  font-weight: bold;
}

.arrow {
  width: 7.5vw;
  height: 7.5vw;
}

.arrow path {
  fill: hsl(200, 50%, 50%);
}

.fade-enter-active,
.fade-leave-active {
  transition: 0.1s all ease-out;
}

.fade-enter,
.fade-leave-to {
  opacity: 0;
}

@media (max-width: 900px) {
  .message {
    font-size: 7vw;
  }

  .arrow {
    width: 10vw;
    height: 10vw;
  }
}

@media (max-width: 600px) {
  .message {
    width: 90vw;
    font-size: 10vw;
  }

  .arrow {
    width: 15vw;
    height: 15vw;
  }
}
</style>
