<template>
  <b-form-group class="form">
    <div class="title">Register</div>
    <form @submit.prevent="validateBeforeSubmit">
      <div class="column is-12">
        <div class="label">Email</div>
        <p class="control has-icon has-icon-right">
          <b-input
            name="email"
            v-model="email"
            v-validate="'required|email'"
            :class="{'input': true, 'is-danger': errors.has('email') }"
            type="text"
            placeholder="Email"
          ></b-input>
          <i v-show="errors.has('email')" class="fa fa-warning"></i>
          <span v-show="errors.has('email')" class="alert">{{ errors.first('email') }}</span>
        </p>
      </div>
      <div class="column is-12">
        <div class="label">Name</div>
        <p class="control has-icon has-icon-right">
          <b-input
            name="name"
            v-model="name"
            v-validate="'required|alpha_spaces|min:3'"
            :class="{'input': true, 'is-danger': errors.has('name') }"
            type="text"
            placeholder="Name"
          ></b-input>
          <i v-show="errors.has('name')" class="fa fa-warning"></i>
          <span v-show="errors.has('name')" class="alert">{{ errors.first('name') }}</span>
        </p>
      </div>
      <div class="column is-12">
        <div class="label">Username</div>
        <p class="control has-icon has-icon-right">
          <b-input
            name="username"
            v-model="username"
            v-validate="'required|alpha_num|min:6'"
            :class="{'input': true, 'is-danger': errors.has('username') }"
            type="text"
            placeholder="Username"
          ></b-input>
          <i v-show="errors.has('username')" class="fa fa-warning"></i>
          <span v-show="errors.has('username')" class="alert">{{ errors.first('username') }}</span>
        </p>
      </div>
      <div class="column is-12">
        <div class="label">Password</div>
        <p class="control has-icon has-icon-right">
          <b-input
            v-validate="'required|min:6'"
            name="password"
            type="password"
            :class="{'input': true, 'is-danger': errors.has('password') }"
            placeholder="Password"
            ref="password"
          ></b-input>
          <span v-show="errors.has('password')" class="alert">{{ errors.first('password') }}</span>
        </p>
      </div>
      <div class="column is-12">
        <div class="label">Confirm Password</div>
        <p class="control has-icon has-icon-right">
          <b-input
            v-validate="'required|confirmed:password'"
            name="password_confirmation"
            type="password"
            :class="{'input': true, 'is-danger': errors.has('password_confirmation') }"
            placeholder="Password again"
            data-vv-as="password"
          ></b-input>
          <span
            v-show="errors.has('password_confirmation')"
            class="alert"
          >{{ errors.first('password_confirmation') }}</span>
        </p>
      </div>

      <b-button class="btn" type="submit">Submit</b-button>
    </form>
  </b-form-group>
</template>

<script>
export default {
  name: "form-example",
  data: () => ({
    email: "",
    name: "",
    phone: "",
    url: ""
  }),
  methods: {
    validateBeforeSubmit() {
      this.$validator.validateAll().then(result => {
        if (result) {
          // eslint-disable-next-line
          alert("Form Submitted!");
          return;
        }

        alert("Correct the errors!");
      });
    }
  }
};
</script>


<style scoped>
.alert {
  color: red;
}
.title {
  margin-left: 20%;
  font-size: 30px;
}
.form {
  margin-left: 30%;
}
.input {
  width: 50%;
}
.btn {
  width: 100px;
  margin-left: 20.5%;
}
</style>
