<template>
  <div>
    <div id="bigcont">
      <img @click="prevImage" class="prev" src="../assets/ancor.png">
      <div id="chat">
        <img :src="chatImage">
      </div>
      <div id="appeal">
        <img :src="currentImage">
      </div>
      <img @click="nextImage" class="next" src="../assets/ancor1.png">
    </div>
    <div class="thumbnails">
      <div
        v-for="(image, index) in  images"
        :key="image.id"
        :class="['thumbnail-image', (activeImage == index) ? 'active' : '']"
        @click="activateImage(index)"
      >
        <button class="bttn">{{ image.thumb }}</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //Array to hold all carousel images
      images: [
        {
          id: 1,
          big: require("../Picture/c1.png"),
          thumb: "Brown",
          chat: require("../Picture/x1.png")
        },
        {
          id: 2,
          big: require("../Picture/c2.png"),
          thumb: "Moon",
          chat: require("../Picture/x2.png")
        },
        {
          id: 3,
          big: require("../Picture/c3.png"),
          thumb: "Kh Kh",
          chat: require("../Picture/x3.png")
        },
        {
          id: 4,
          big: require("../Picture/c4.png"),
          thumb: "Nhanh Nhu",
          chat: require("../Picture/x4.png")
        },
        {
          id: 5,
          big: require("../Picture/c5.png"),
          thumb: "B Bng",
          chat: require("../Picture/x5.png")
        },
        {
          id: 6,
          big: require("../Picture/c6.png"),
          thumb: "Mt Ngt",
          chat: require("../Picture/x6.png")
        },
        {
          id: 7,
          big: require("../Picture/c7.png"),
          thumb: "Tht M",
          chat: require("../Picture/x7.png")
        }
      ],
      activeImage: 0
      //Index of the active image on the images array
    };
  },

  computed: {
    // currentImage gets called whenever activeImage changes
    // and is the reason why we don't have to worry about the
    // big image getting updated
    currentImage() {
      return this.images[this.activeImage].big;
    },
    chatImage() {
      return this.images[this.activeImage].chat;
    }
  },
  methods: {
    nextImage() {
      var active = this.activeImage + 1;
      if (active >= this.images.length) {
        active = 0;
      }
      this.activateImage(active);
    },
    prevImage() {
      var active = this.activeImage - 1;
      if (active < 0) {
        active = this.images.length - 1;
      }
      this.activateImage(active);
    },
    activateImage(imageIndex) {
      this.activeImage = imageIndex;
    }
  }
};
</script>


<style scoped>
#bigcont {
}
#chat {
  margin-left: 300px;
  margin-bottom: -400px;
}
.prev {
  margin-bottom: -700px;
  margin-left: 100px;
  width: 100px;
  height: 100px;
}
.prev:hover {
  opacity: 0.6;
}
.next {
  margin-top: -700px;
  margin-left: 90%;
  width: 100px;
  height: 100px;
}
.next:hover {
  opacity: 0.6;
}
#appeal {
  margin-top: 100px;
  margin-left: 400px;
}
.thumbnails {
  margin-left: 400px;
}
.active {
  background-color: rgb(60, 194, 105);
  border-radius: 100px;
}

.prev {
  left: 5px;
}
.next {
  right: 5px;
}
.bttn {
  border: dodgerblue;
  color: white;
  height: 120px;
  width: 120px;
  opacity: 0.6;
  border-radius: 100px;
  text-decoration-style: bold;
  background-image: radial-gradient(circle, green, blue);
  box-shadow: 10px 10px 5px black;
  outline: none;
}
.thumbnail-image {
  display: inline-block;
  justify-content: center;
  align-items: center;
}
.active {
  opacity: 0.6;
}
</style>
