    <div class="content2">
      <img id="object3" src="../assets/bmgioithieu.png">
      <div class="video">
        <youtube :video-id="videoId" ref="youtube" @playing="playing"></youtube>
      </div>
      <img
        :class="{'object5': scrolled}"
        v-on="handleScroll"
        class="sta"
        src="../Picture/sidepic.png"
      >
